body {
  font-family: Arial, sans-serif;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3.5rem;
  margin-bottom: 3.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  max-width: 2000px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* The main container in Home & Contact pages */
.container {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#root {
  width: 100%;
}
/* =========================================
   HEADER / NAVBAR
========================================= */
.app-header {
  text-align: center;
  padding: 1rem 0;
  background-color: #fff;
/*  border-bottom: 1px solid #ccc;*/
}

/* "My Portfolio" title */
.app-header h1 {
  margin: 0.5rem 0;
  font-size: 2.5rem;
}

/* NAVBAR with top & bottom lines */
.navbar {
  margin-top: 0.5rem;
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
  padding: 0.5rem 0;
  margin-top: 20px;
  width: 100%;
}

.navbar ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 0;
  margin: 0;
}

/* Navbar links */
.navbar a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: color 0.2s ease;
}
.navbar a:hover {
  color: #007BFF;
}

/* =========================================
   PAPERS SECTION
========================================= */
.papers {
  width: 100%;
  margin-top: 1rem;
/*  margin-bottom: 1rem;*/
}

.paper {
  background-color: #f9f9f9;
  padding: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 4px;
}

.paper h3 {
  margin: 0 0 0.2rem 0;
}

/* 
  Download buttons:
  - Show a flag to the left
  - "Download" text
  - On hover: gray out background & show a download arrow
*/
.download-links {
  display: flex;
  flex-wrap: wrap; /* so they stack on smaller screens */
  gap: 1rem;
}

/* Button styling */
.download-btn {
  display: inline-flex;
  align-items: center;
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 0.4rem 1rem;
  cursor: pointer;
  position: relative;
  transition: background-color 0.2s ease;
  font-weight: 500;
}

/* Flag icon */
.flag-icon {
  width: 24px;
  height: 16px;
  margin-right: 0.5rem;
}

/* On hover, button grays out and show arrow */
.download-btn:hover {
  background-color: #666;
}

.fi {
  font-size: 20px;
}

