/* =========================================
   DATA VISUALIZATIONS
========================================= */
.visualizations {
  width: 100%;
  margin-bottom: 3rem;
}

.visualization-gallery {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
}

.visualization-card {
  background-color: #ececec;
  padding: 0.5rem;
  text-align: center;
  border-radius: 4px;
  width: 100%;
}

/* A small caption under each video/image */
.viz-caption {
  margin-top: 0.5rem;
  font-size: 0.9rem;
  color: #555;
}

/* Responsive tweak: row layout if wide enough */
@media (min-width: 768px) {
  .visualization-gallery {
    flex-direction: row;
    justify-content: space-around;
  }
}

/* ---------------------- */

.visualizations {
  width: 100%;
  margin-bottom: 3rem;
}

.visualization-gallery {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
}

/* 
  Each card has a max-width to prevent it from stretching too far.
  Center it with margin: 0 auto if desired.
*/
.visualization-card {
  background-color: #ececec;
  padding: 0.5rem;
  text-align: center;
  border-radius: 4px;
  width: 100%;
  max-width: 100%; /* Change to any max width you want */
  margin: 0 auto;
  margin-bottom: 1rem;
}

/* 
  For the video (or any image) to stay responsive while keeping its aspect ratio:
  - width: 100% and height: auto ensures the aspect ratio is maintained.
  - max-height (or max-width) prevents it from getting too big.
  - object-fit: contain helps ensure the entire video fits if the container is smaller.
*/
.responsive-media {
  width: 100%;
  height: auto;
  max-width: 100%;
  max-height: 700px; /* Adjust to your desired maximum height in px */
  object-fit: contain; 
  display: block;      /* Ensures the element respects block layout rules (e.g., margins) */
  margin: 0 auto;
}

/* A small caption under each video/image */
.viz-caption {
  margin-top: 0.5rem;
  font-size: 0.9rem;
  color: #555;
}

/* Responsive tweak: row layout if wide enough */
@media (min-width: 768px) {
  .visualization-gallery {
    flex-direction: row;
    justify-content: space-around;
  }
}

/* =========================================
   BLOG PAGE
========================================= */

/* The Blog page wrapper 
   - max width 700, left-aligned
*/
.blog-container {
  max-width: 700px;
  margin: 0 auto;
  text-align: left;
  padding-top: 1rem; /* from your instructions */
  width: 100%;
}

.blog-container section {
  padding-top: 0.6rem;
  padding-bottom: 0.6rem;
}

.blog-container p {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.blog-container a {
  font-size: 1.6rem;
}

/* =========================================
   CONTACT PAGE (unchanged)
========================================= */

.container p {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.contact-box {
  border: 1px solid #ccc;
  padding: 10px;
  width: 250px; /* Adjust width as needed */
  text-align: left;
  border-radius: 4px; /* Optional: slightly rounded corners */
  background-color: #f9f9f9; /* Optional: subtle background color */
  margin: 0 auto;
  margin-top: 1rem;
}

/* Style the paragraphs to sit side by side with icons */
.contact-box p {
  display: flex;
  align-items: center;
  margin: 8px 0;
}

/* Common icon styling */
.icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 8px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

/* Replace the URL strings below with actual icon image URLs or data URIs */
.mail-icon {
  background-image: url('../email.png');
}

.twitter-icon {
  background-image: url('../logo-black.png');
}

/* Optional hover effect for links */
.contact-box a {
  color: blue;
  text-decoration: none;
}

.contact-box a:hover {
  text-decoration: underline;
}

/*about container*/

.about-container {
  width: 100%;
  margin: 0 auto;    /* Centers the container if it has a fixed width; redundant for 100% but good practice */
  text-align: center; /* Centers text and inline content inside the container */
}
